import React from 'react';

import { FaCheck } from 'react-icons/fa';
import { RiCloseFill } from 'react-icons/ri';
import { BsFillTrashFill } from 'react-icons/bs';


const Todo = props => {
    return (
        <div className="todo" >
            <p style={{ textDecoration: props.todo.completed ? 'line-through' : '' }}>
                {props.todo.name}
            </p>
            <div className="btns">
                <button disabled={props.todo.completed} onClick={() => props.handleCompleted(props.index)}><FaCheck /></button>
                <button disabled={!props.todo.completed} onClick={() => props.handleInCompleted(props.index)}>< RiCloseFill /></button>
                <button onClick={() => props.handleDelete(props.index)}>< BsFillTrashFill /></button>
            </div>
        </div>
    );
}

export default Todo;